import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			token: '',
			status: 0,
			tip: '',
			sum: 0,
			list: [],
			page: 1,
			complete: true,
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.getData(1);
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom() {
		if (!this.complete) {
			this.page++;
			this.getData(this.page);
		}
	},
	methods: {
		//菜单tab切换事件
		changeStatus(status) {
			this.status = status;
			this.list = [];
			this.getData(1);
		},
		//拨打师傅电话
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},
		//获取数据
		getData(page) {
			this.request({
				url: interfaces.orderList,
				data: {
					page: page,
					status: this.status
				},
				header: {
					token: this.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						
						if (page == 1) {
							this.list = [];
						}

						this.sum=res.sum;

						if (!common.isBlank(res.data)) {
							if (res.data.length < res.num) {
								this.complete = true;
								this.tip = '数据已全部加载，暂无其他数据';
							} else {
								this.complete = false;
								this.tip = '上拉加载更多';
							}

							for (var k in res.data) {
								res.data[k].create_time_txt = common.formatDT(res.data[k].create_time, 'yyyy.MM.dd h:m');
							}

							this.list = this.list.concat(res.data);
							this.page = page;


						} else {
							this.complete = true;
							this.tip = '数据已全部加载，暂无其他数据';
						}

					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},

		//连锁店客户或普通客户确认完工
		finish(id) {
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认维修好了吗？',
				success: function(res) {
					if (res.confirm) {
						this.request({
							url: interfaces.orderFinish,
							header: {
								token: _this.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									
									_this.status = 8;
									_this.getData(1);
									
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},

		//跳转订单详情页
		goDetail(id) {
			wx.navigateTo({
				url: 'detail?id=' + id,
			})
		},

	}
}
